	import uParse from '@/components/gaoyia-parse/parse.vue'
	
	import {
		mapState,
		
	} from 'vuex'
	export default {
		components:{
			uParse
		},
		computed: mapState(['articleid','avatarUrl','session_key','nickName']), //对全局变量监控
		data() {
			return {
				html:'',
				article:'',
				comments:[],
				commentContent:''
			}
		},
		onLoad() {
			uni.request({
				url: 'http://127.0.0.1:8081/blog/index/intoArticle',
				data:{
					id:this.articleid
					
				},
				success: (res) => {
					this.article=res.data
					console.log(res.data);
				}
					
				
			});
			this.loadComment();
		},
		methods: {
			 preview(src, e) { 
      // do something
    },
    navigate(href, e) {
      // do something
    },
	
	loadComment(){
			uni.request({
			url: 'http://127.0.0.1:8081/blog/index/article/loadComment',
			data:{
				id:this.articleid,
				
				
			},
			success: (res) => {
				this.comments=res.data
				console.log("res.data");
				console.log(res)
			}
				
			
		})
		
		
	},
	addComment(){
		if(this.commentContent==''){
		uni.showToast({
			title:'请输入内容',
			image:'../../static/img/fail.png',
			duration:1000
		})
		
		return;
		}
		
			uni.request({
			url: 'http://127.0.0.1:8081/blog/index/article/addComment',
			data:{
				articleId:this.articleid,
				session_key:this.session_key,
				commentContent:this.commentContent
				
				
			},
			success: (res) => {
				uni.showToast({
					title:res.data.msg,
					duration:1000
				});
				this.loadComment();
			},
			fail: (res) => {
				uni.showToast({
					title:res.data.msg,
					image:'../../static/img/fail/png',
					duration:1000
				})
			}
				
			
		});
		this.commentContent='';
		
	},
	deleteComment(commentid){
			uni.request({
			url: 'http://127.0.0.1:8081/blog/index/article/deleteComment',
			data:{
				articleId:this.articleid,
				session_key:this.session_key,
				commentid:commentid
				
				
			},
			success: (res) => {
				uni.showToast({
					title:res.data.msg,
					duration:1000
				});
				this.loadComment();
			},fail: (res) => {
				uni.showToast({
					title:res.data.msg,
					image:'../../static/img/fail/png',
					duration:1000
				})
			}
				
			
		});
		
		
	},
	addlike(){
		uni.request({
			url: 'http://127.0.0.1:8081/blog/index/article/addLike',
			data:{
				articleId:this.articleid,
				session_key:this.session_key,
				
				
				
			},
			success: (res) => {
				uni.showToast({
					title:res.data.msg,
					duration:1000
				});
				this.loadComment();
			},
			fail: (res) => {
				uni.showToast({
					title:'收藏失败',
					image:'../../static/img/fail/png',
					duration:1000
				})
			}
				
			
		});
		
		
	},
	addgive(){
			uni.request({
			url: 'http://127.0.0.1:8081/blog/index/article/addGive',
			data:{
				articleId:this.articleid,
				session_key:this.session_key,
				
				
				
				
			},
			success: (res) => {
				uni.showToast({
					title:res.data.msg,
					duration:1000
				});
				
			},
			fail: (res) => {
				uni.showToast({
					title:'收藏失败',
					image:'../../static/img/fail/png',
					duration:1000
				})
			}
				
			
		});
	}
	
			
		}
	}
